import React, { useState } from 'react'
import { Link } from "react-router-dom";

export default function Navbar() {

    const [mode, setMode] = useState('-translate-y-80')
    const toggleStyle = () => {
        if (mode === '-translate-y-80') {
            setMode('-translate-y-0')
        }
        else{
            setMode('-translate-y-80')
        }

    }
    return (
        <div className="navbar z-[100] fixed flex flex-col md:flex-row w-screen justify-between bg-none bg-black px-0 md:px-10">
            <div className="brand z-[51] text-3xl md:text-4xl lg:text-5xl xl:text-6xl font-bold flex py-3 px-10 bg-black text-white brand-font">Soumyadeep
                <div id="nav-button" className={`absolute left-auto right-10 inline md:hidden space-y-1 translate-y-3 cursor-pointer`} onClick={toggleStyle}>
                    <div id="l1" className="bg-white w-7 rounded-full h-1"></div>
                    <div id="l2" className="bg-white w-7 rounded-full h-1"></div>
                    <div id="l3" className="bg-white w-7 rounded-full h-1"></div>
                </div>
            </div>
            <div className={`text-font z-[50] absolute duration-200 md:static mt-14 ${mode} md:-translate-y-0 flex flex-col text-lg lg:text-xl xl:text-2xl md:flex-row px-11 py-6 md:px-0 md:my-auto gap-5 md:gap-10 xl:gap-14 bg-black w-screen md:w-fit`}>
                <Link to="/"><div className="item hover:underline bg-black underline-white text-white underline-offset-8 font-semibold">About</div></Link>
                <Link to="/skills"><div className="item hover:underline bg-black underline-white text-white underline-offset-8 font-semibold">Skills</div></Link>
                <Link to="/projects"><div className="item hover:underline bg-black underline-white text-white underline-offset-8 font-semibold">Projects</div></Link>
                <Link to="/education"><div className="item hover:underline bg-black underline-white text-white underline-offset-8 font-semibold">Education</div></Link>
                <Link to="/contact"><div className="item hover:underline bg-black underline-white text-white underline-offset-8 font-semibold">Contact</div></Link>
            </div>
        </div>
    )
}
